import styled from 'styled-components';

export const Wrapper = styled.div`
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  a {
    color: #212121;
  }
`;

export const Brand = styled.div`
  display: grid;
  width: 200px;
  grid-template-columns: 70px auto;
`